import '../../less/common.less';
import './index.less';

import $ from 'jquery';
import '../../js/nav';
import '../../js/switch-lang';

function initDom() {
    var tabs_pos = $(".tabs").offset();
    // initially, everything but first tab is hidden
    $('.tab-content').addClass('hidden');
    $('.tab-content.progress').removeClass('hidden').addClass('show');
    $('.tab.progress').addClass('selected');
    // selected option in menu
    $('.tab').on('click', function() {
        console.log("i'm in");
        $('.tab').removeClass('selected');
        $(this).addClass('selected');
    });

//   $('div.top').click(function() {
//   $('html, body').animate({
//     scrollTop: $("div.middle").offset().top
//   }, 1000)
// })
    // switch content displayed on selection
    $('.tab.progress').on('click', function() {
        $('.tab-content').removeClass('show').addClass('hidden');
        $('.tab-content.progress').removeClass('hidden').addClass('show');
    });
    $('.tab.influence').on('click', function() {
        $('.tab-content').removeClass('show').addClass('hidden');
        $('.tab-content.influence').removeClass('hidden').addClass('show');
    });
    $('.tab.coin').on('click', function() {
        $('.tab-content').removeClass('show').addClass('hidden');
        $('.tab-content.coin').removeClass('hidden').addClass('show');
    });
    $('.tab.rewards').on('click', function() {
        $('.tab-content').removeClass('show').addClass('hidden');
        $('.tab-content.rewards').removeClass('hidden').addClass('show');
    });

    // scroll to tab content
    $('.tab').on('click', function(e) {
        $('html, body').animate({
            scrollTop: tabs_pos.top
        }, 600);
        console.log("completed");
        return false
    });
}

initDom();





